import { NextRequest, NextResponse } from 'next/server';

export async function POST(req: NextRequest) {
  try {
    const { name = 'No Name', email = 'No Email', subject = 'No Subject', message = 'No Message' } = await req.json();

    const response = await fetch('https://api.resend.com/emails', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${process.env.RESEND_API_KEY}`, // server-side secret
      },
      body: JSON.stringify({
        from: process.env.RESEND_EMAIL,
        to: 'mwaduzzaman@gmail.com', // replace with your actual recipient email
        subject,
        html: `
          <p><strong>Name:</strong> ${name}</p>
          <p><strong>Email:</strong> ${email}</p>
          <p><strong>Message:</strong> ${message}</p>
        `,
      }),
    });

    // Log response JSON for debugging
    const resJson = await response.json();
    console.log('Resend API response:', resJson);

    if (!response.ok) {
      return NextResponse.json(
        { success: false, error: resJson?.error || 'Failed to send email' },
        { status: 500 }
      );
    }

    return NextResponse.json({ success: true });
  } catch (error: any) {
    console.error('Error in sending email:', error);
    return NextResponse.json(
      { success: false, error: error.message || 'Internal Server Error' },
      { status: 500 }
    );
  }
}
